import React from "react";
import { useResetInput } from "../../hook/useResetInputs";

/**
 *  input de pesquisa
 * @param {*} refParam ref do use ref
 * @returns JSX.Element
 */
function SearchCLient({ refParam }) {
  useResetInput();
  return (
    <div>
      <input
        ref={refParam}
        placeholder="pesquise através do identificador do usuário"
      />
    </div>
  );
}

export default SearchCLient;
