import styled from "styled-components";

export const Container = styled.div`
  position: relative;
  width: 100%;
  margin: 20px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h1 {
    text-align: center;
  }

  form {
    background: #e6e6e0;
    padding: 20px;
    width: 500px;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    box-shadow: 5px 10px #5dbb44;
    font-size: 16px;

    input {
      border: none;
      border-bottom: 2px solid #000;
      background: none;
      padding: 5px 0;
      font-size: 1.3rem;

      &:focus {
        background: #bdbdad;
      }
    }

    button {
      width: 150px;
      margin: 20px auto;
      padding: 15px 0;
      background: linear-gradient(90deg, #338a08, #42b309);
      border: none;
      border-radius: 25px;
      font-weight: bold;
      font-size: 16px;
      transition: background 1s;
      transition: color 0.5s;

      &:hover {
        background: #205705;
        color: #c9c9c9;
      }
      &:active {
        transform: translateY(3px);
      }
    }
  }

  @media (max-width: 720px) {
    form {
      width: 95vw;
      border-top: none;
      border-bottom: none;
      border-radius: 20px;
      box-shadow: none;
      border: 1px solid #5dbb44;
    }
  }
`;
export const Pagr = styled.span`
  margin: 0;
  background: blue;
  height: 20px;
  visibility: ${(props) => (props.visible ? "visible" : "hidden")};
  padding-left: 3px;
  background: tomato;
  font-weight: bold;
  transition: all 0.3s;
`;
