import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Dashboard from '../Pages/Dashboard/index';
import ShowClients from '../Pages/showClients';
import AddClient from '../Pages/AddClientAndUpdate';

function Routes() {
  return (
    <Switch>
      <Route path="/" exact component={Dashboard} />
      <Route path="/show_clients" component={ShowClients} />
      <Route path="/add_client" component={AddClient} />
      <Route path="/update_client/:params" component={AddClient} />
    </Switch>
  );
}

export default Routes;
