import React from "react";
import { Link } from "react-router-dom";
import * as s from "./styles";

function Dashboard() {
  return (
    <>
      <s.Container>
        <s.Content>
          <s.TitleContainer>
            <h1>Menu de navegação</h1>
            <h2>Olá, seja bem-vindo(a)</h2>
          </s.TitleContainer>
          <s.MenuNavigation>
            <s.Items>
              <Link to="/show_clients">
                <img loading='lazy' src="https://img.icons8.com/ios-glyphs/60/000000/search-client.png"/>
                <h3>Pesquisar Clientes</h3>
              </Link>
            </s.Items>
            <s.Items>
              <Link to="/add_client">
              <img loading='lazy' src="https://img.icons8.com/material/50/000000/add-user-group-man-man--v1.png"/>
                <h3>Adicionar novo cliente</h3>
              </Link>
            </s.Items>
          </s.MenuNavigation>
        </s.Content>
      </s.Container>
    </>
  );
}

export default Dashboard;
