import styled from "styled-components";

export const Container = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;

`;
export const Content = styled.div`
  margin: 20px 0;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 80%;
  background: #fff;
  border: 1px solid #42b309;
  padding: 10px;
  h1 {
    text-align: center;
  }

  > button {
    margin: 5vh 5vw;
    max-width: 100px;
    padding: 5px 10px;
    background: linear-gradient(90deg, #338a08, #42b309);
    border: none;
    border-radius: 25px;
    font-weight: bold;
    font-size: 12px;
    transition: background 1s;
    transition: color 0.5s;

    &:hover {
      background: #205705;
      color: #c9c9c9;
    }
  }

  table {
    width: 900px;
    margin: 0 auto;
    background: none; // #e6e6e0

    thead {
      background: #bbbbaa;
      text-align: center;
      th {
        padding: 5px;
      }
    }

    tbody {
      position: relative;
      tr {
        td {
          padding: 5px;
        }
      }
    }
  }
  @media (max-width: 1299px) {
   
    overflow: auto;
    width: 90%;
    > button {
      padding: 10px 25px;
      font-size: 16px;
    }
  }
`;
export const EditTable = styled.span`
  width: 25px;
  /* height: 25px; */
  background: none;
  display: flex;
  align-items: center;
  button {
    background: #e6e6e0;
    border: none;
    margin-left: 5px;
    padding-top: 2px;

    img {
      width: 20px;
      height: 20px;
    }
    &:hover {
      background: #bbbbaa;
    }
  }
`;

export const TableData = styled.td`
  background: #e6e6e0;
  min-width: 150px;
  min-height: 50px;
  text-align: center;
  font-size: 16px;
  cursor: ${(props) => (props.contentEditable ? "pointer" : "default")};

  button {
    background: none;
    border: none;
  }
`;
export const InvisibleComponentName = styled.div`
  left: 70px;
  top: 115px;
  position: absolute;

  .name {
    top: 115px;
    display: block;
    margin: 0 auto;
    top: 30px;
    right: 100px;
    height: 350px;
    width: 600px;
  }
  div {
    position: fixed;
    overflow: hidden;
    transition: height 0.25s ease-in-out;
    height: 400px;
  }

  div.hide {
    height: 0;
    position: absolute;
    opacity: 0; /**por entar combinar o p. absolute como fixed, cria-se um pequeno desvio, onde a div hide joga o form pro canto direito da tela, usei o opacity enquanto hide para o usuário não identificá-lo. Vale para as demais divs. */
  }
  @media (max-width: 720px) {
    .name {
      top: 20%;
      right: 25%;
    }
  }
`;

export const Loading = styled.span`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;
export const ButtonWrapper = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  button {
   
    border: solid 1px #e6e6e0;

    &:hover {
      background: #ccc;
      transition: all 0.3s;
    }
  }
  span {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 10px;
    padding: 0 5px;

    img {
      max-width: 20px;
    }
  }

  @media (max-width: 720px) {
    /* flex-direction: column; */
    button {
      img {
        max-width: 25px;
      }
    }
  }
`;
export const SearchPacker = styled.div`
  margin: 10px 0;
  width: 100%;
  display: flex;
  align-items: center;
  /* flex-wrap: wrap; */
  gap: 5px;
  button {
    display: flex;
    align-items: center;
    flex-direction: column;
    background: #ccc;
    border: 1px solid #ccc;
    width: 25px;
    height: 23.5px;
    img {
      max-width: 20px;
    }
  }

  input {
    background: #ccc;
    border: none;
    width: 300px;
    height: 23.5px;
    padding: 0 0 0 5px;
    &::-webkit-input-placeholder  { 
      color:#000;
      font-weight: 500;
    }
    &:focus {
      border: 1px solid #42b309;
      box-shadow: 0 0 1px 1px #42b309;
      transition: all 0.3s;
    }
  }

`;
export const ButtonPackerAndButton = styled.div`

  max-width: 1300px;
  display: flex;
  gap: 20px;
  margin: 10px auto;
  
  justify-content: center;
  @media (max-width: 558px) {
    display: block;
  }
 
`;
