import React, { useState, useRef } from "react";
import { useHistory } from "react-router";
import ReactLoading from "react-loading";
import { toast } from "react-toastify";
import api from "../../services/api";
import * as s from "./styles";
import icomTrashCan from "../../assests/image/icons/trash-can.png";
import SearchCLient from "../../components/SearchCLient";
import { useLoad } from "../../hook/useLoad";

function SearchClientComponent({ valorInput, searchById }) {
  return (
    <s.SearchPacker>
      <SearchCLient
        refParam={valorInput}
       
      />
      <button
        type="button"
        onClick={() => searchById(valorInput.current.value)}
      >
        <img src="https://img.icons8.com/material-rounded/48/000000/search-folder.png" />
      </button>
    </s.SearchPacker>
  );
}
function FullListOfClients({ deleteClient }) {
  const [datas, setData] = useState([]);
  const { load, setLoad } = useLoad();
  const history = useHistory();
  let valorInput = useRef(null);

  async function deleteClient(e) {
    try {
      setLoad(true);
      const { clientes, message } = await api.delete(`client/delete/${e}`);
      if (clientes.length) {
        const identification = datas.filter(
          (client) => client.identificador !== e
        );
        // o filter pecorrer o datas e retorna, em array, os dados que passaram no teste
        setData(identification);
        toast.success("cliente excluído com sucesso");
        console.log(identification);
      } else {
        toast.success(message);
      }
    } catch (err) {
      toast.error(`error: ${err}`);
    }
    setLoad(false);
  }

  async function loadTheWholeList(id) {
    setLoad(true);
    try {
      let data = id
        ? await api.get(`/client/search/${id}`)
        : await api.get("/client/findall");

      const { clientes, message } = data.data;
      if (clientes) {
        setData([...clientes]);
      } else {
        toast.error("message");
      }
    } catch (error) {
      toast.error("falha na consulta");
      console.error("error :>> ", error);
    }
    setLoad(false);
  }
  async function searchById(id) {
    //ignora se não houver id
    if(!id){return}
    setLoad(true);
    try {
      let data = await api.get(`/client/search/${id}`);
        
      const { clientes, message } = data.data;
      if (clientes) {
        setData([...clientes]);
      } else {
        toast.error("message");
      }
    } catch (error) {
      toast.error("falha na consulta");
      console.error("error :>> ", error);
    }
    setLoad(false);
  }

  return (
    <>
      <s.ButtonPackerAndButton>
        <SearchClientComponent
          valorInput={valorInput}
          searchById={searchById}
        />
        <s.ButtonWrapper>
          <button type="button" onClick={() => loadTheWholeList()}>
            <span>
              <img src="https://img.icons8.com/ios-filled/50/000000/list.png" />
              carrega todos os clientes
            </span>
          </button>
        </s.ButtonWrapper>
      </s.ButtonPackerAndButton>
      {load ? (
        <s.Loading>
          <ReactLoading type="balls" width="64px" height="64px" color="#000" />
        </s.Loading>
      ) : (
        datas.length > 0 && (<table>
          <thead>
            <tr>
              <th>
                <strong>IDENTIFICADOR</strong>
              </th>
              <th>
                <strong>NOME</strong>
              </th>
              <th>
                <strong>NOME DE USUÁRIO</strong>
              </th>

              <th>
                <strong>ATUALIZAÇÃO</strong>
              </th>
            </tr>
          </thead>
          <tbody>
            {datas.map((client) => (
              <tr key={client.identificador}>
                <s.TableData>
                  {client.identificador}
                </s.TableData>
                <s.TableData>{client.nome}</s.TableData>
                <s.TableData>
                  {" "}
                  {client.nomeDeUsuario}
                </s.TableData>
                <s.TableData>
                  {client.ultimoAcesso}
                </s.TableData>
                <s.EditTable>
                  <button
                    type="button"
                    onClick={() => deleteClient(client.identificador)}
                  >
                    <img
                      src={icomTrashCan}
                      alt="deletar usuário"
                      title="deletar usuário"
                    />
                  </button>
                  <button
                    title='editar usuário'
                    type="button"
                    onClick={() =>
                      history.push(
                        `/update_client/${client.identificador}~${client.nome}~${client.nomeDeUsuario}`
                      )
                    }
                  >
                    <img src="https://img.icons8.com/material-rounded/48/000000/edit-file.png"/>
                  </button>
                </s.EditTable>
              </tr>
            ))}
          </tbody>
        </table>)
      )}
    </>
  );
}

function CustomerInfo() {
  return (
    <>
      <s.Container>
        <s.Content>
          <h1>Visualizar clientes</h1>
          <FullListOfClients />
        </s.Content>
      </s.Container>
    </>
  );
}

export default CustomerInfo;
